@import '~antd/dist/antd.css';

body{
  font-family: 'Nunito', sans-serif !important;
}


.icon i {
  position: relative;
  font-size: 200%;
  /* left: 50%; */
  /* top: 100%; */
  height: 70%;
  top: 50%;
  transform: translateY(-25%);
  background-color: none;
  /* transition: 1s; */
}

.icon{
  color: #ffffff;
  transform:translateY(-50%) !important;
  border-radius:50px;
  position:absolute;
  text-align: center;
  left:calc(-36px + 50%);
  top:50px;
  width:72px;
  height:72px;
  background-image: linear-gradient(120deg, #ad3094, #333399);
  /* border:1px solid #500050; */
  transition: 0.25s;
  cursor:pointer;
}

.footerBox{
  background-color: black;
}

